<template>
  <div style="text-align:center;">
    <h3 class="mdl-color-text--primary">Add Raw Material</h3>
    <raw-material-form ref="form">
    </raw-material-form>
    <router-link
      tag="a"
      class="custom-button"
      :to="{ name: 'rawMaterials.listView' }"
    >
     Back
    </router-link>
    <button
      class="mdl-button mdl-js-button mdl-button--raised mdl-js-ripple-effect mdl-button--colored custom-button--primary"
      @click="createRawMaterial">Add</button>

  </div>
</template>
<script>
import { RawMaterialForm } from '.'
import { mapActions } from 'vuex'
import { MESSAGES } from '@/constants'

export default {
  components: {
    RawMaterialForm
  },
  computed: {
    form: {
      get () {
        return this.$refs.form
      }
    }
  },
  methods: {
    ...mapActions([
      'addRawMaterial'
    ]),
    createRawMaterial () {
      this.form.valid().then(valid => {
        if (valid) {
          this.addRawMaterial(this.form.rawMaterialParams).then(() => {
            this.$toastr.s(MESSAGES.success_add)
            this.$router.push({ name: 'rawMaterials.listView' })
          }).catch(e => {
            this.$toastr.e(MESSAGES.form_verify)
          })
        }
      })
    }
  }
}
</script>



